import { isPlatform } from '@ionic/react';
import Girisdesktop from "./pages/giris/girisDesktop"
import GirisIos from "./pages/giris/girisIos"
import GirisAndroid from "./pages/giris/girisAndroid"

/* Theme variables */
import './theme/variables.css';


const Default: React.FC = () => {
  if (isPlatform("ios")) {
    return (
      <GirisIos />
    )
  }

  else if (isPlatform("android")) {
    return (
      <GirisAndroid />
    )
  }

  else if (isPlatform("desktop")) {
    return (
      <Girisdesktop />
    )
  }

  else {
    return (
      <div>Cihaznz E-Ynetici le Uyumlu Deil. Ltfen, Anroid, IOS veya Desktop iletim sistemli bir cihazla giri yapnz.</div>
    )
  }
}


export default Default;
